	}
	100% {
		right: 100%;
	}
}
@keyframes bgRight {
  0% {
		right: 0
	}
	100% {
		right: 100%;
	}
}
/*pulse*/
.pulse{
	animation-name: pulse;
	-webkit-animation-name: pulse;
	animation-iteration-count: infinite;
	-webkit-animation-iteration-count: infinite;
}
@keyframes pulse {
	0% {
		transform: scale(0.9);
	}
	50% {
		transform: scale(1);
	}
	100% {
		transform: scale(0.9);
	}
}
@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(0.95);
		opacity: 0.7;
	}
	50% {
		-webkit-transform: scale(1);
		opacity: 1;
	}
	100% {
		-webkit-transform: scale(0.95);
		opacity: 0.7;
	}
}
/*spin*/
.spin{
  animation-name: spin;
  -webkit-animation-name: spin;
  animation-iteration-count: infinite;
	-webkit-animation-iteration-count: infinite;
  animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
}
@keyframes spin {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
	from {
		-webkit-transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
	}
}
.bgSlideLeft{
  -webkit-animation-name: bgSlideLeft;
  animation-name: bgSlideLeft;
  z-index: -1
}
@-webkit-keyframes bgSlideLeft {
  0% {
		left: 100%
	}
	100% {
		left: 0
	}
}
@keyframes bgSlideLeft {
  0% {
		left: 100%
	}
	100% {
		left: 0
	}
}
.bgSlideRight{
  right: 0;
  left: auto;
  -webkit-animation-name: bgSlideRight;
  animation-name: bgSlideRight;
  z-index: -1
}
@-webkit-keyframes bgSlideRight {
  0% {
		right: 100%
	}
	100% {
		right: 0
	}
}
@keyframes bgSlideRight {
  0% {
		right: 100%
	}
	100% {
		right: 0
	}
}
