.stickbottom {
  position: fixed;
  bottom: -87px;
  left: 0;
  right: 0;
  background-color: #ff464e;
  box-shadow: 0 5px 18px 0 rgba(0, 0, 0, 0.39);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 87px;
  overflow: hidden;
  transition: bottom .5s ease;
  z-index: 4;
}

.stickbottom-text {
  font-size: 20px;
  font-weight: bold;
  color: #FFFFFF;
}

.stickbottom-text .btn-stickbottom {
  display: inline-block;
  vertical-align: middle;
  line-height: normal;
  margin-top: 0;
  margin-left: 10px;
  font-size: 14px;
  font-weight: bold;
  padding: 16px 24px;
  background-color: #FFFFFF;
  color: #ff464e;
}

.stickbottom-text .btn-stickbottom:hover {
  background-color: #63b19e;
  color: #FFFFFF;
}

.stickexpand {
  position: fixed;
  background-color: #63b19e;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.36);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 90px;
  top: 275px;
  right: 0;
  padding-left: 105px;
  padding-right: 15px;
  border-top-left-radius: 90px;
  border-bottom-left-radius: 90px;
  transition: .5s ease;
  z-index: 11;
}

.stickexpand:hover {
  width: 275px;
}

.stickexpand::before {
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: absolute;
  top: 50%;
  left: 22px;
  width: 71px;
  height: 71px;
  margin-top: -36px;
}

.stickexpand-text {
  position: relative;
  color: #FFFFFF;
  font-weight: bold;
  flex: 1 1;
}

.stickexpand-text::after {
  content: "DAFTAR DI SINI";
  background-repeat: no-repeat;
  background-position: right center;
  background-size: auto 8px;
  background-size-color: #be1522;
  display: table;
  font-size: 11px;
  padding-right: 16px;
  margin-top: 5px;
}

.stickexpand.resiko {
  top: 390px;
}

.stickexpand.resiko .stickexpand-text::after {
  content: "CEK DI SINI";
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 11;
  pointer-events: none;
  -webkit-transition: opacity 0.5s ease;
  -webkit-transition-transition: opacity 0.5s ease;
  opacity: 0;
}

.popup.show {
  opacity: 1;
  pointer-events: auto;
}

.popup.show .popup-box {
  opacity: 1;
  transform: scale(1);
}

.popup-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%;
  padding: 30px;
}

.popup-box {
  background-color: #FFFFFF;
  box-shadow: 0 5px 18px 0 rgba(0, 0, 0, 0.39);
  max-width: 564px;
  padding: 60px;
  position: relative;
  opacity: 0;
  flex: 1 1;
  transform: scale(0.9);
  transform-transition: .5s ease;
  border-radius: 15px;
}

.popup-box.popup-banner {
  padding: 0;
}

.popup-box.popup-banner img {
  width: 100%;
}

.popup-box.popup-banner .popup-close {
  right: 5px;
  top: 5px;
}

.popup-close {
  position: absolute;
  right: 15px;
  top: 15px;
  background-color: #ff464e;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 18px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  transition: .3s ease;
}

.popup-close:hover {
  background-color: #63b19e;
}

