    transition: transform 0.5s ease, opacity 0s ease 0.5s;
    justify-content: center;
    flex-flow: column;
    opacity: 0;
  }
  .header .container .header-right .mtop > a,
  .header .container .header-right .nav > li,
  .header .container .header-right .msosmed {
    transition: transform 0.5s ease, opacity 0.5s ease;
    transform: translate3d(0, -20px, 0);
    opacity: 0;
  }
  .header .container .header-right .mtop {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding-top: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header .container .header-right .mtop a {
    padding-left: 15px;
    padding-right: 15px;
  }
