    display: flex;
    align-items: center;
    justify-content: center;
    height: 66px;
  }
  .header .container .header-right .msosmed {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
  }
  .header .container .header-right .msosmed-item {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 36px;
    height: 36px;
    display: block;
    margin-left: 1em;
  }
  .header .container .header-right .msosmed-item:first-child {
    margin-left: 0;
  }
  .header .container .header-right.open {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    transition: transform 0.5s ease, opacity 0s ease 0s;
    opacity: 1;
  }
  .header .container .header-right.open .mtop > a,
  .header .container .header-right.open .nav > li,
  .header .container .header-right.open .msosmed {
    opacity: 1;
    -webkit-transform: translate3d(0, 0px, 0);
    transform: translate3d(0, 0px, 0);
  }
  .header .container .header-right.open .nav > li {
    transition-delay: 0.6s;
  }
  .header .container .header-right.open .nav > li:first-child + li {
    transition-delay: 0.7s;
  }
  .header .container .header-right.open .nav > li:first-child + li + li {
    transition-delay: 0.8s;
  }
  .header .container .header-right.open .nav > li:first-child + li + li + li {
    transition-delay: 0.9s;
  }
  .header .container .header-right.open .nav > li:first-child + li + li + li + li {
    transition-delay: 1s;
  }
  .header .container .header-right.open .nav > li:first-child + li + li + li + li + li {
    transition-delay: 1.1s;
  }
  .header .container .header-right.open .nav > li:first-child + li + li + li + li + li + li {
    transition-delay: 1.2s;
  }
  .header .container .header-right.open .nav > li:first-child + li + li + li + li + li + li + li {
    transition-delay: 1.3s;
  }
  .header .container .header-right.open .msosmed {
    transition-delay: 1.4s;
  }
  .header .container .header-right.open .mtop .logo.toss {
